import './remind.css'
import React, { useState, useEffect } from "react"
import axios from "axios"
// import DateTimePicker from "react-datetime-picker"
// import DatePicker from "react-datepicker"
// import "react-datepicker/dist/react-datepicker.css";

function Remind() {

  const [ reminderMsg, setReminderMsg ] = useState("")
  const [ remindAt, setRemindAt ] = useState()
  const [ reminderList, setReminderList ] = useState([])

  useEffect(() => {
      axios.get("http://localhost:9000/getAllReminder").then( res => setReminderList(res.data))
  }, [])



  const addReminder = () => {
      axios.post("http://localhost:9000/addReminder", { reminderMsg, remindAt })
      .then( res => setReminderList(res.data))
      setReminderMsg("")
      setRemindAt()
  }

  const deleteReminder = (id) => {
    axios.post("http://localhost:9000/deleteReminder", { id })
    .then( res => setReminderList(res.data))
  }

  return (
    <div className="App">
      <div className="homepage">

        <div className="homepage_header">
          <h1>Remind Me </h1>
          <input type="text" placeholder="Reminder notes here..." value={reminderMsg} onChange={e => setReminderMsg(e.target.value)} />
          <input type='datetime-local' onChange={e => setRemindAt(e.target.value)} />
          {/* <DatePicker 
         value={remindAt}
         onChange={date => setRemindAt(date)}
         showTimeSelect
         timeFormat="HH:mm"
         timeIntervals={15}
         timeCaption="time"
         dateFormat="MMMM d, yyyy h:mm aa"
          /> */}
          <div className="button" onClick={addReminder}>Add Reminder</div>
        </div> 


        <div className="homepage_body">
          {
            reminderList.map( reminder => (
              <div className="reminder_card" key={reminder._id }>
                <h2>{reminder.reminderMsg}</h2>
                <h3>Remind Me at:</h3>
                <p>{String(new Date(reminder.remindAt))}</p>
                <div className="button" onClick={() => deleteReminder(reminder._id)}>Delete</div>
              </div>
           )) 
          }
        </div>
      </div>
    </div>
  )
}

export default Remind;